function show() {
    let more = document.querySelector('.btntofollow')
    if (more) {
        window.addEventListener('scroll', ()=>{
            if (window.scrollY > 10) {
                more.style.opacity = '0'
            }else{
                more.style.opacity = '1'
            }
        })
        more.addEventListener('click', ()=>{
            window.scrollTo({
                top: window.innerHeight-100,
                behavior: "smooth"
            })
        })
    }
}

export default show