import iObserve from "./components/observer";
import itemObserver from "./components/itemObserver";
import sidebar from "./components/sidebar";
import show from "./components/window";
import topper from "./components/topper";
import Slider from "./components/slider";

try {
    show()
    topper()
} catch (error) {
    
}

itemObserver([[...document.querySelectorAll('.home0 .item')], [...document.querySelectorAll('.home1 .item')]])
iObserve([...document.querySelectorAll('.hidden')]);
iObserve([...document.querySelectorAll('.organisations .box div')])
sidebar()

let perfect = new Slider(
    {slides: 'slide', 
    slidesContainer: 'slides', 
    prevBtn: 'btn-prev', 
    nextBtn: 'btn-next', 
    sliderBox: 'slider',
    wait: 2000}
)

perfect.run()

let perfect2 = new Slider(
    {slides: 'item', 
    slidesContainer: 'slides', 
    prevBtn: 'btn-prev', 
    nextBtn: 'btn-next', 
    sliderBox: 'slider2',
    wait: 3000}
)

perfect2.run()

